.row {
    /*margin: 5px;*/
    width: 100%;
    display: inline-block;
}
.block {
    display: block;
    margin-left: 5px;
    margin-right: 5px;
    background-color: #fafafa;
    border-radius: 15px;
    box-shadow: 0px 6px 15px -14px rgba(0,0,0,0.75);
    min-height: 15px;
    max-width: 70%;
    margin-top: 5px;
    padding: 7px;
    padding-left: 10px;
    font-size: 14px;
}
.block.dark {
    background-color: #2c2d2e;
    box-shadow: 0px 6px 15px -14px rgba(255,255,255,0.75);
    color: #eaeaea;
}

.block.left {
    float: left;
}
.block.right {
    float: right;
}

.author {
    font-weight: bold;
    color: #488EDF;
    font-size: 12px;
    margin-right: 4px;
}
.text {
    margin-top: 4px;
}
.time {
    font-size: 8px;
    margin-left: 5px;
    margin-right: 5px;
    float: right;

}