.circle {
    background-color: white;
    height: 28px;
    width: 28px;
    padding: 10px;
    /*border: 1px solid gray;*/
    border-radius: 28px;
    box-shadow: 0px 6px 30px -12px rgba(0,0,0,0.75);
    display: block;
    position: fixed;
    bottom: 80px;
    right: 10px;
    z-index: 1000;
    cursor: pointer;
    color: #3c6a9e;
}
.arrow{
    transform: rotate(-90deg) translateX(2px);
}